.cart-dropdown {
  position: absolute;
  width: 260px;
  height: 340px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border: 1px solid black;
  background-color: white;
  top: 90px;
  right: 40px;
  z-index: 5;

  .cart-items {
    height: 240px;
    display: flex;
    flex-direction: column;
    overflow: scroll;

    .empty-message {
      margin: 0 auto; 
      margin-top: 100px;
      font-size: 18px;
    }
  }

  button {
    margin-top: auto;
  }
}
