.checkout-page {
  width: 55%;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px auto 0;

  .checkout-header {
    width: 100%;
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid darkgrey;

    .header-block {
      text-transform: capitalize;
      width: 23%;

      &:last-child {
        width: 8%;
      }
    }
  }

  .total {
    margin-top: 30px;
    margin-left: auto;
    font-size: 36px;
  }

  button {
    margin-left: auto; 
    margin-top: 50px;
  }

  .test {
    text-align: center;
    margin-top: 30px;
    font-size: 24px;
    color: red;
  }

  @media screen and (max-width: 800px) {
    width: 95%;

    .checkout-header {
     display: none;
    }

    .total {
      font-size: 25px;
    }

    .test { 
      font-size: 18px;
    }

    button {
      margin-top: 30px;
    }
  }
}
