.header {
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;

  @media screen and (max-width: 800px) {
    height: 60px;
    padding: 10;
    margin-bottom: 20px;
  }

  .logo-container {
    height: 100%;
    width: 70px;
    padding: 25px;

    @media screen and (max-width: 800px) {
      width: 50px;
      padding: 0;
    }
  }

  .options {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    @media screen and (max-width: 800px) {
      width: 90%;
      font-size: 14px;
    }

    .option {
      padding: 10px 15px;
      color: #000;
      text-decoration: none;
      cursor: pointer;

      &:hover {
        color: #888;
        transition: .5s;
      }
    }
  }
}